const app = initializeApp(firebaseConfig); //      Google Firebase

refs.signUpButton.addEventListener('click', () => {
  refs.authorizationWindowSubmitButton.textContent = 'sign up';
  refs.signUpButton.classList.add('curr');
  refs.signInButton.classList.remove('curr');
  refs.inputName.disabled = false;
  refs.inputName.style.display = 'block';
}); //      "sign up"

refs.signInButton.addEventListener('click', () => {
  refs.authorizationWindowSubmitButton.textContent = 'sign in';
  refs.signUpButton.classList.remove('curr');
  refs.signInButton.classList.add('curr');
  refs.inputName.disabled = true;
  refs.inputName.style.display = 'none';
  refs.authorizationWindowForm.elements.name.value = '';
}); //      "sign in"

const auth = getAuth(app); //    "auth"  Auth,    @firebase 

refs.authorizationWindowForm.addEventListener('submit', e => {
  e.preventDefault(); //     
  if (
    refs.authorizationWindowSubmitButton.textContent.trim().toLowerCase() ===
    'sign up'
  ) {
    signUpWithEmailPassword();
  } else {
    onSignIn();
  }
}); //  "submit"  

function signUpWithEmailPassword() {
  const name = `${refs.authorizationWindowForm.elements.name.value}`;
  const email = `${refs.authorizationWindowForm.elements.email.value}`;
  const password = `${refs.authorizationWindowForm.elements.password.value}`;

  // [START auth_signup_password]
  try {
    createUserWithEmailAndPassword(auth, email, password)
      .then(userCredential => {
        // Signed in
        user = userCredential.user; //  
        // console.log(user.uid);
        userID = user.uid;
        refs.btnLogin.classList.add('visually-hidden'); //   "Sign up"
        refs.btnSigned.classList.remove('visually-hidden'); //   "User"
        refs.btnLoginTextSigned.textContent = name; //    "User" ' 
        refs.authorization.classList.toggle('is-hidden'); //   
        refs.authorizationWindowForm.reset(); //  
        document.body.style.overflow = 'scroll'; //    
        Notify.success(
          `User ${name} with email address ${email} successfully created!`
        ); //     
        writeUserName(name); //      
        localStorage.setItem('user-name', `${name}`); //       
        localStorage.setItem('user-id', `${userID}`); //  id     
        refs.headerNav.classList.remove('visually-hidden'); //   "Home"  "ShoppingList"
        // ...
      })
      .catch(error => {
        const errorCode = error.code;
        const errorMessage = error.message;
        console.log(errorCode);
        console.log(errorMessage);

        if (errorCode === 'auth/wrong-password') {
          Notify.failure(`Wrong password.`); //    
        } else if (errorCode === 'auth/email-already-in-use') {
          Notify.failure(`User with this email ${email} already in use.`); //    
        } else {
          Notify.failure(`${errorMessage}`); //    
        }
        console.log(error);
        // ..
      });
  } catch (error) {
    console.log(error);
    Notify.failure(`User ${name} with email address ${email} failed created!`);
  } //     
}

function onSignIn() {
  const name = `${refs.authorizationWindowForm.elements.name.value}`;
  const email = `${refs.authorizationWindowForm.elements.email.value}`;
  const password = `${refs.authorizationWindowForm.elements.password.value}`;
  if (email.length < 4) {
    Notify.failure(
      `User email address is wrong! Please enter an email address.`
    ); //     
    return;
  }
  if (password.length < 4) {
    Notify.failure(`User password is wrong! Please enter a password.`);
    return;
  } //    

  try {
    // Sign in with email and pass.
    signInWithEmailAndPassword(auth, email, password)
      .then(userCredential => {
        // Signed in
        user = userCredential.user; //  
        // console.log(user.uid);
        userID = user.uid;
        refs.btnLogin.classList.add('visually-hidden'); //   "Sign up"
        refs.btnSigned.classList.remove('visually-hidden'); //   "User"
        refs.btnLoginTextSigned.textContent = name; //    "User" ' 
        refs.authorization.classList.toggle('is-hidden'); //   
        document.body.style.overflow = 'scroll'; //    
        refs.authorizationWindowForm.reset(); //  
        Notify.success(
          `User ${name} with email address ${email} successfully SIGNED!`
        ); //     

        localStorage.setItem('user-id', `${userID}`); //  id     
        refs.headerNav.classList.remove('visually-hidden'); //   "Home"  "ShoppingList"

        reedNameUserDB()
          .then(response => {
            localStorage.setItem('user-name', `${response}`); //       
            refs.btnLoginTextSigned.textContent = response; //  '       
          })
          .catch(error => {
            console.log(error.message);
          }); //         
      })
      .catch(error => {
        const errorCode = error.code;
        const errorMessage = error.message;
        // console.log(errorCode);
        console.log(errorMessage);
        if (errorCode === 'auth/wrong-password') {
          Notify.failure(`Wrong password!`); //    
        } else {
          Notify.failure(`${errorMessage}`); //    
        }
        console.log(error);
        // ..
      });
  } catch (error) {
    console.log(error);
    Notify.failure(`User ${name} with email address ${email} failed SIGNED!`);
  } //    
}

refs.btnLogout.addEventListener('click', onSignOut); //   Log out

refs.btnSigned.addEventListener('click', () => {
  refs.btnLogout.classList.toggle('visually-hidden'); //   "Log out"
}); //     "User"    "Log out"

function onSignOut() {
  try {
    auth
      .signOut()
      .then(() => {
        refs.btnLogin.classList.remove('visually-hidden'); //   "Sign up"
        refs.btnSigned.classList.add('visually-hidden'); //      
        refs.btnLogout.classList.add('visually-hidden'); //   "Log out"
        userID = '';
        Notify.success(
          `User ${refs.btnLoginTextSigned.textContent} successfully SIGNED OUT!`
        ); //    

        document.location.replace('index.html'); // Log out,  click            
        refs.headerNav.classList.add('visually-hidden'); //   "Home"  "ShoppingList"
        localStorage.setItem('user-name', ``); //       
        localStorage.setItem('user-id', ``); //  id     
      })
      .catch(error => {
        console.log(error);
        Notify.failure(
          `User ${refs.btnLoginTextSigned.textContent} with email address ${email} failed SIGNED OUT!`
        ); //     
      });
  } catch (error) {
    console.log(error);
    Notify.failure(
      `User ${refs.btnLoginTextSigned.textContent} with email address ${email} failed SIGNED OUT!`
    ); //     
  }
}

